<template>
  <div id="app">
    <div id="hook"></div>
    <Navbar/>
    <Carousel/>
    <router-view/>
    <audio :src="audioSrc" muted loop preload id="bgmusic">
      music error
    </audio>
    <button 
      class="MusicBtn" 
      @click="MusicControl()"
       
    >
      <b-icon :icon="iconName"  scale="2" id="AudioIcon"></b-icon>
    </button>
    

    <button
      class="GoTopBtn"
      v-scroll-to="'#hook'"
    >
      <b-icon icon="chevron-bar-up" scale="2"></b-icon>
    </button>
    
    <End/>
    <loading :active.sync="isLoading"></loading>
  </div>
</template>

<script>
import End from './components/End.vue'
import Navbar from './components/Navbar/navbar.vue'
import Carousel from './components/Carousel/carousel.vue'


export default {
  name: 'App',
  components: {
    End,
    Navbar,
    Carousel,
  },
  data() {
    return {
      audioSrc: require("./assets/audio/S.T.A.Y..mp3"), // 背景音樂
      iconName: 'volume-mute-fill', // 預設音樂ICON
      isLoading: true, // 設定資源載入時為true，預設為真
    }
  },
  methods:  {
    MusicControl() {
      let audio = document.getElementById("bgmusic")
      if(audio.paused){
        audio.muted = false
        this.iconName = 'volume-up-fill'
        audio.play()
      }else{
        audio.muted = true
        this.iconName = "volume-mute-fill"
        audio.pause()
      }
    },
  },
  mounted() {
    let audio = document.getElementById("bgmusic")
    audio.addEventListener('canplaythrough', (Event) => {
      this.isLoading = false
    })
  }
}
</script>

