import Vue from 'vue'
import VueRouter from 'vue-router'
import Info from '../views/Info.vue'
import Video from '../views/Video.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '/info',
    name: 'Info',
    component: Info
  },
  {
    path: '/video',
    name: 'Video',
    component: Video
  },
  {
    path: '*',
    redirect: '/info',
  },
]

const router = new VueRouter({
  routes
})

export default router
