      controls
      indicators
      background="#ababab"
      img-width="1024"
      img-height="480"
      fade
      style="text-shadow: 1px 1px 2px #333"
      @sliding-start="onSlideStart"
      @sliding-end="onSlideEnd"
    >
      <!-- Text slides with image -->
      <b-carousel-slide
        caption="Julie J. Morley"
        text="The universe’s imagination always remains wider than our human imaginations."
        img-src="https://imgur.com/sFKHCRk.png"
      ></b-carousel-slide>

      <b-carousel-slide
        caption="Albert Einstein"
        text="Only two things are infinite, the universe and human stupidity, and I’m not sure about the former."
        img-src="https://imgur.com/N1mwzDW.png"
      ></b-carousel-slide>

      <b-carousel-slide
        caption="Rumi"
        text="Do not feel lonely, the entire universe is inside of you."
        img-src="https://imgur.com/odrBzHS.png"
      ></b-carousel-slide>

      <b-carousel-slide
        caption="Blaise Pascal"
        text="The eternal silence of these infinite spaces terrifies me."
        img-src="https://imgur.com/XOyQgkw.png"
      ></b-carousel-slide>

      <b-carousel-slide
        caption="Carl Sagan"
        text="The universe seems neither benign nor hostile, merely indifferent."
        img-src="https://imgur.com/Zm7AtLE.png"
      ></b-carousel-slide>

    </b-carousel>
  </div>
</template>

<script>
export default {
  data() {
    return {
      slide: 0,
      sliding: null,
    };
  },
  methods: {
    onSlideStart(slide) {
      this.sliding = true;
    },
    onSlideEnd(slide) {
      this.sliding = false;
    },
  },
};
</script>

